$dark: false !default;

// frankly speaking, these are the only color variables that need to be changed
$color-foreground: #182023 !default;
$color-background: #f5f6f7 !default;
$color-primary: #2685bf !default;
$color-error: #f0441e !default;

$color-text-normal: $color-foreground !default;
$color-text-reversed: $color-background !default;

$color-foreground-1: null;
$color-foreground-2: null;
$color-foreground-3: null;
$color-foreground-4: null;
$color-foreground-5: null;

$color-background-1: null;
$color-background-2: null;
$color-background-3: null;
$color-background-4: null;
$color-background-5: null;

$color-primary-1: null;
$color-primary-2: null;
$color-primary-3: null;
$color-primary-4: null;
$color-primary-5: null;

$color-error-1: $color-error;

$color-text-primary: null;
$color-text-secondary: null;
$color-text-disabled: null;
$color-text-error: null;

$color-text-reversed-primary: null;
$color-text-reversed-secondary: null;
$color-text-reversed-disabled: null;
$color-text-reversed-error: null;

@if $dark == false {
  $color-foreground-1: $color-foreground;
  $color-foreground-2: lighten($color-foreground, 20%);
  $color-foreground-3: lighten($color-foreground, 40%);
  $color-foreground-4: lighten($color-foreground, 60%);
  $color-foreground-5: lighten($color-foreground, 80%);

  $color-background-1: $color-background;
  $color-background-2: darken($color-background, 20%);
  $color-background-3: darken($color-background, 40%);
  $color-background-4: darken($color-background, 60%);
  $color-background-5: darken($color-background, 80%);

  $color-primary-1: $color-primary;
  $color-primary-2: lighten($color-primary, 20%);
  $color-primary-3: lighten($color-primary, 30%);
  $color-primary-4: lighten($color-primary, 40%);
  $color-primary-5: lighten($color-primary, 50%);

  $color-text-primary: rgba($color-text-normal, 0.87);
  $color-text-secondary: rgba($color-text-normal, 0.54);
  $color-text-disabled: rgba($color-text-normal, 0.38);;
  $color-text-error: rgba($color-error, 0.87);

  $color-text-reversed-primary: rgba($color-text-reversed, 1.0);
  $color-text-reversed-secondary: rgba($color-text-reversed, 0.7);
  $color-text-reversed-disabled: rgba($color-text-reversed, 0.5);;
  $color-text-reversed-error: rgba($color-error, 1.0);
} @else {
  $color-foreground-1: $color-foreground;
  $color-foreground-2: darken($color-foreground, 20%);
  $color-foreground-3: darken($color-foreground, 40%);
  $color-foreground-4: darken($color-foreground, 60%);
  $color-foreground-5: darken($color-foreground, 80%);

  $color-background-1: $color-background;
  $color-background-2: lighten($color-background, 20%);
  $color-background-3: lighten($color-background, 40%);
  $color-background-4: lighten($color-background, 60%);
  $color-background-5: lighten($color-background, 80%);

  $color-primary-1: $color-primary;
  $color-primary-2: darken($color-primary, 20%);
  $color-primary-3: darken($color-primary, 30%);
  $color-primary-4: darken($color-primary, 40%);
  $color-primary-5: darken($color-primary, 50%);

  $color-text-primary: rgba($color-text-normal, 1.0);
  $color-text-secondary: rgba($color-text-normal, 0.7);
  $color-text-disabled: rgba($color-text-normal, 0.5);;
  $color-text-error: rgba($color-error, 1.0);

  $color-text-reversed-primary: rgba($color-text-reversed, 0.87);
  $color-text-reversed-secondary: rgba($color-text-reversed, 0.54);
  $color-text-reversed-disabled: rgba($color-text-reversed, 0.38);;
  $color-text-reversed-error: rgba($color-error, 0.87);
}



